<template>
	<div>
		<div class="video-box">
			<video class="video-background" preload="auto" loop playsinline autoplay
				src="https://lf3-cdn-tos.bytescm.com/obj/eden-cn/lmeh7pfuho/campus/campus_intro_20200522.mp4"
				tabindex="-1" muted="muted"></video>
		</div>
		<div class="login">
			<h1 class="h1">K</h1>
			<!-- 关闭键 -->
			<i class="el-icon-close" style="
			    position: absolute;
			    right: 0;
			    top: 0;
			    font-size: 30px;
			    color: #b5b4b4;
			    cursor: pointer;
			  " @click="changeShowLogin()"></i>
			<!-- 登录表单 -->
			<el-form label-width="0px" class="logForm" :model="login_form" :rules="loginFromRules" ref="loginFormRef"
				@keyup.enter.native="toLogin()">
				<el-form-item prop="phone">
					<el-input class="input1" placeholder="请输入11位手机号" prefix-icon="el-icon-user"
						v-model="login_form.phone" />
				</el-form-item>
				<el-form-item prop="password">
					<el-input class="input2" placeholder="请输入密码" prefix-icon="el-icon-lock"
						v-model="login_form.password" show-password />
				</el-form-item>
			</el-form>
			<div class="logBtn" @click="toLogin()">登录</div>
			<div class="regBtn" @click="toRegister()">没有账号？免费注册</div>

		</div>
	</div>
</template>

<script>
	import Cookies from 'js-cookie'
	export default {
		name: "login",
		components: {},
		data() {
			return {
				//登录时的表单绑定
				login_form: {
					phone: "",
					password: "",
				},
				//登录的表单验证规则
				loginFromRules: {
					//验证账号
					phone: [{
						required: true,
						message: "请输入11位手机号",
						trigger: "blur",
					}, ],
					//验证密码
					password: [{
							required: true,
							message: "请输入密码",
							trigger: "blur",
						},
						{
							trigger: "blur",
							validator: (rule, value, callback) => {
								let passwordreg = /(?=.*\d)(?=.*[a-zA-Z])(?=.*[^a-zA-Z0-9]).{8,16}/;
								if (!passwordreg.test(value)) {
									callback(
										new Error("密码必须有数字、字母、特殊符号组成,请输入八到十六位")
									);
								} else {
									callback();
								}
							}

						}
					],
				},
			};
		},
		methods: {
			//点击登录
			toLogin() {
				var phone = this.$store.state.phone;
				var password = this.$store.state.password;
				
				if (this.login_form.phone == "" || this.login_form.password == "") {
					alert("请输入手机号和密码")
				}else if (phone == this.login_form.phone && password == this.login_form.password) {
					this.$router.push({
						name: "musicHome",
						params: {
							phone: this.login_form.phone,
							password: this.login_form.password,
						},
						
					});
					this.$notify({
						title: "登录成功",
						type: "success",
						message: "欢迎" + this.login_form.phone + "成功登录了！",
						duration: 5000
					})
					//this.$store.commit("login",this.login_form.phone);
				}else{
					alert("该帐号未注册！！请检查您的号码或您的密码")
				}
				
				},
				changeShowLogin() {
					this.$router.push({
						name:"musicHome",
					});
					/* this.$router.go(-1) */
				},
				toRegister() {
					this.$router.push({
						name: "register",
					});
				},
			},
			
		
	}
</script>

<style>
	.video-box {
		position: relative;
		height: 100vh;
		background-color: #C1CFF7;
		/*进行视频裁剪*/
		overflow: hidden;
	}

	.video-box .video-background {
		position: absolute;
		left: 50%;
		top: 50%;
		/*保证视频内容始终居中*/
		transform: translate(-50%, -50%);
		width: 100%;
		height: 100%;
		/*保证视频充满屏幕*/
		object-fit: cover;
		min-height: 800px;
	}

	.login {
		z-index: 500;
		position: fixed;
		width: 550px;
		height: 500px;
		left: 50%;
		top: 10%;
		background: #fff;
		background: rgb(0 0 0 /40%);
		box-shadow: #cecccc 0px 0px 2px 2px;
		transform: translate(-50%);
		border-radius: 3%;	
	}
	
	.logForm {
		position: absolute;
		width: 350px;
		height: 150px;
		left: 20%;
		top: 43%;
		
	}	
	
	.logBtn {
		position: absolute;
		width: 60%;
		padding: 10px 15px;
		background: whitesmoke;
		text-align: center;
		color: black;
		cursor: pointer;
		border-radius: 20px;
		top: 78%;
		left: 52%;
		transform: translateX(-50%);
		/* background-color: rgb(0 0 0 /40%); */
		opacity: 0.8;
	}
	.logBtn:hover{
		cursor: pointer;
		color:#c72e2e;
	}	
	
	.regBtn {
		position: absolute;
		padding: 15px 15px;
		font-family: 黑体;
		font-size: 13px;
		color:white;
		top: 78%;
		left: 50%;
		transform: translate(0, 70%);
	}
		
	.regBtn:hover {
		cursor: pointer;
		color: #c72e2e;
	}
		
	.input1 {
		margin-bottom: 20px;
	}	
	
	.input2 {
		margin-top: 10px;
	}
		
	.el-form-item__label {
		/* 给el-form组件的label标签颜色修改 */
		color: #FFFFFF;
	}	
	
	.el-input__inner {
		color: #FFFFFF;
		/* 使input框的背景变透明 */
		background-color: transparent;
		/* 使边框也变透明 */
		border-color: transparent;
		/* 给边框加阴影能够使其有立体感 */
		box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.2);
		/* 改变获取焦点后的竖线颜色 */
		caret-color: rgba(0, 255, 255, 0.8);
		color: #FFFFFF;
	}
		
	.el-input__inner:hover {
		border-color: rgb(0, 255, 255);
	}
		
	.el-input__inner:focus {
		border-color: aqua;
	}	
	
	.h1 {
		background-image: -webkit-linear-gradient(left, #ffe7eb, #ff757e  20%, #ff5566 40%,  #ff5566 60%,#ff757e  80%, #ffe7eb 100%);
		-webkit-text-fill-color: transparent;
		-webkit-background-clip: text;
		-webkit-background-size: 200% 100%;
		-webkit-animation: masked-animation 4s linear infinite;
		font-size: 180px;
		text-align: center;
		margin-top: 10px;
	}
		
	@keyframes masked-animation {
		0% {
			background-position: 0 0;
		}
		
		100% {
			background-position: -100% 0;
		}
	}	
</style>
